.home-mobile-box {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background-color: #f5f5f5;
}

.home-mobile-userinfo {
  width: 100%;
  height: 220px;
  background-color: #fff;
  display: flex;
  align-items: center;
}

.hmu-avatar {
  margin-left: 30px;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #f5f5f5;
}

.hmu-right {
  margin-left: 20px;
  height: 90px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hmu-name {
  font-size: 40px;
  color: #333;
}

.hmu-study {
  display: flex;
  align-items: center;
}

.hmu-study > span:nth-of-type(1) {
  font-size: 22px;
  color: #717481;
}

.hmu-study > span:nth-of-type(2) {
  margin-left: 20px;
  font-size: 24px;
  color: #333;
}

.home-mobile-function,
.home-mobile-exam {
  margin-top: 20px;
  width: 100%;
  background-color: #fff;
}

.hmf-title,
.hme-title {
  padding: 20px 30px;
  font-size: 30px;
  color: #333;
  border-bottom: 1px solid #f0f0f0;
}

.hmf-content,
.hme-content {
  padding: 10px 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.hmfc-item {
  padding: 20px 0;
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hmfc-item > img {
  width: 70px;
  height: 70px;
}

.hmfc-item > span {
  margin-top: 30px;
  font-size: 26px;
  color: #333;
}

.hme-content {
  padding: 20px 0 50px 0;
  display: flex;
}

.hmec-mock-exam {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hmec-mock-exam > img {
  width: 200px;
  height: 200px;
}

.hmec-mock-exam > span {
  margin-top: 30px;
  padding: 20px 20px;
  font-size: 26px;
  color: #fff;
  background-color: #18b4ed;
  border-radius: 8px;
}

.hmec-exam-ant-btn {
  margin-top: 30px;
}
